html, body, header, footer, article, section, h1, h2, h3, h4, h5, h6, div, span, p, ul, ol, li, form, input, textarea, a {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
    
  outline: none;
}
a { text-decoration: none; }
ul, ol { list-style: none; }
html, body {
    overflow-x: hidden;
    color: rgb(93, 93, 93);
}

:root {
    /* Theme colors */
    --font-color: rgb(93, 93, 93);
    --link-color: rgb(18, 134, 255);
    --nav-pink: #fc029e;
    --nav-blue: #3c99ff;
    
    --footer-orange: #ff8e00;
    
    --grey: #fcfcfc;
    --dark-grey: rgb(214, 214, 214);
    --light-black: #333;
    
    /* Page */
    --page-margin: 136px;
    --page-line-height: 1.5;
    
    /* Header elements */
    
    --nav-height: 100px;
    --nav-font: sans-serif;
    --nav-font-size: 17px;
    --nav-font-weight: 800;
    
    --nav-footer-padding: 70px;
    
    --hamburger-width: 79px;
    --hamburger-line-height: 6px;
    --hamburger-line-margin: 11.5px;
    --hamburger-height: calc((var(--hamburger-line-height) * 3) + (var(--hamburger-line-margin) * 2));
    --hamburger-scale: 1;
    
    --logo-wrapper-width: 330px;
    --logo-wrapper-height: 150px;
    --logo-wrapper-border-radius: 30px; 
    
    --logo-width: 252px;
    --logo-height: 167px;
    --logo-width: 181px;
    --logo-height: 120px;
    --logo-scale: 1;
    
    /* Body */
    --header-margin: 68px;
    
    --icon-size: 192px;
    --icon-margin: 110px;
    
    --icon-with-text-size: 49px;
    --icon-with-text-margin-right: 28px;
    --icon-with-text-margin-bottom: 50px;
    
    --gallery-width: 1112px;
    --gallery-grid-gap: 10px;
    --gallery-picture-width: 250px;
    --gallery-picture-height: 250px;
    --gallery-picture-padding: 5px;
    
    --gallery-padding-top: 75px;
    --gallery-padding-side: 50px;
    
    --gallery-screen-height: 500px;
    --gallery-screen-max-width: 800px;
    --gallery-screen-close-handle-size: 44px;
    --gallery-screen-control-height: 110px;
    --gallery-screen-control-width: 35px;
    --gallery-screen-border-radius: 15px;
    
    --form-input-height: 50px;
    --form-textarea-height: 160px;
    
    --list-style-size: 47px;
    --list-style-padding: 70px;
    --list-style-margin: 50px;
    
    --list-padding: 136px;
    --list-left: 65%;
    --list-right: 35%;
    
    --ref-columns: 3;
    
    --paragraph-font: sans-serif;
    --paragraph-font-size: 16px;
    --paragraph-font-weight: 100;
    
    /* Footer elements */
    --footer-height: 70px;
    --footer-font-size: 15px;
    --footer-link-space: 36px;
}

body {
    padding-top: var(--nav-height);
}

html {
    
}

.navigation.top {
    top: calc(-1 * var(--nav-height));
}

.navigation {
    position: fixed;
    top: 0;
    left: 0;
    
    width: 100%;
    height: var(--nav-height);
    background: white;
    z-index: 10;
}
.navigation, .navigationLinks {
    height: var(--nav-height);
}

.navigationLinks {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    
    font-family: var(--nav-font);
    line-height: var(--nav-height);
    font-size: var(--nav-font-size);
    font-weight: var(--nav-font-weight);
    
    text-transform: uppercase;
    padding: 0 var(--nav-footer-padding);
}
.navigationLinks > li > a {
    color: var(--nav-pink);
}
.navigationLinks > li > a:hover {
    color: var(--nav-blue);
}

.navigationLinks > li > a:active, .navigationLinks > li > a.active {
    color: var(--nav-blue);
}

.logo {
    position: absolute;
    
    top: calc((var(--logo-wrapper-height) - var(--logo-height)) / 2);
    left: calc((var(--logo-wrapper-width) - var(--logo-width)) / 2);
    width: var(--logo-width);
    
}
.logoWrapper {
    position: absolute;
    left: calc((100% - var(--logo-wrapper-width)) / 2);
    
    width: var(--logo-wrapper-width);
    height: var(--logo-wrapper-height);
    
    background: white;
    border-radius: var(--logo-wrapper-border-radius);
    
    z-index: 5;
}
.scaleLogo {
    -webkit-transform: scale(0.7);
        -ms-transform: scale(0.7);
            transform: scale(0.7);
    top: -30px;
}

.Slider {
    position: relative;
    height: 200px;
    width: 100%;
    overflow: hidden;
}
.relativeWrapper {
    position: relative;
    height: 498px;
}
.BigPicture {
    position: absolute;
    width: 100%;

    top: 0;
    left: 0;
}

.BigPicture.active {
    opacity: 1;
    z-index: 2;
}

.BigPicture.inactive {
    opacity: 0;
    z-index: 1;
}

.mainContent {

}

.mainContent .section {
    padding: calc(var(--page-margin) / 2) var(--page-margin);
}
.mainContent .section:first-child {
    padding-top: var(--page-margin);
}
.mainContent .section:last-child {
    padding-bottom: var(--page-margin);
}
.mainContent .section {
    padding: var(--page-margin);
}

.flexRow {
    display: -ms-flexbox;
    display: flex;
}


h1, h2, h3, h4, h5, h6 {
    color: var(--nav-blue);
/*    word-break: break-all;*/
}
h1 {
    margin-bottom: var(--header-margin);
}

.icon {
    -ms-flex-negative: 0;
        flex-shrink: 0;
    width: var(--icon-size);
    height: var(--icon-size);
    margin-top: calc(var(--icon-margin) / 2.4);
    margin-right: var(--icon-margin);
}

p, .paragraph {
    font-family: var(--paragraph-font);
    font-size: var(--paragraph-font-size);
    font-weight: var(--paragraph-font-weight);
    line-height: var(--page-line-height);
    
    white-space: pre-line;
}

.list {
    padding: 0 var(--list-padding);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
    
    margin-top: var(--page-margin);
}

.list .listItem {    
    position: relative;
    
    font-family: var(--nav-font);
    font-size: var(--nav-font-size);
    font-weight: var(--nav-font-weight);
    line-height: var(--list-style-size);
    color: var(--nav-blue);
    
    padding-left: var(--list-style-padding);
    margin-bottom: var(--list-style-margin);
}
.list .listItem:nth-last-child(-n+2) {
    margin-bottom: 0;
}
.list .listItem:nth-child(odd) {
    --column-size: calc(var(--list-left) + (-1 * var(--list-style-padding)));
    -ms-flex: 0 0 var(--column-size);
        flex: 0 0 var(--column-size);
}
.list .listItem:nth-child(even) {
    --column-size: calc(var(--list-right) + (-1 * var(--list-style-padding)));
    -ms-flex: 0 0 var(--column-size);
        flex: 0 0 var(--column-size);
}

.list .listItem:before {
    content: '';
    position: absolute;
    left: 0;
    background: url('/icons/star.svg');
    width: var(--list-style-size);
    height: var(--list-style-size);
    
}

.contact {
    display: -ms-flexbox;
    display: flex;
    margin-top: var(--page-margin);
}

.contactInfo {
    -ms-flex: 0 0 50%;
        flex: 0 0 50%;
}

.contactFormWrapper {
    -ms-flex: 0 0 50%;
        flex: 0 0 50%;
}

.contactInfo .iconWithText {
    margin-bottom: var(--icon-with-text-margin-bottom);
    white-space: pre-line;
}
.contactInfo .iconWithText img {
    max-width: var(--icon-with-text-size);
    max-height: var(--icon-with-text-size);
    vertical-align: middle;
    
    margin-right: var(--icon-with-text-margin-right);
}
.textWrapper {
    display: inline-block;
}
.contactInfo .iconWithText:last-child {
    margin-top: -8px;
}
.contactInfo .iconWithText:last-child .textWrapper {
    position: relative;
    line-height: 1.5;
    top: 14px;
}



.contactForm {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.contactForm label {
    position: relative;
}
input, textarea {
    border: none;
    border-bottom: 2px solid var(--nav-blue);
}
input:focus, textarea:focus {
    border-color: var(--nav-pink);
}
.contactForm label input {
    width: 100%;
    height: var(--form-input-height);
    line-height: var(--form-input-height);
}
.contactForm label textarea {
    width: 100%;
    height: var(--form-textarea-height);
    padding: 15px 0;
