}
.contactForm label .formInputHint {
    position: absolute;
    top: 0;
    left: 0;
    
    z-index: 1;
    color: var(--nav-pink);
    font-weight: 100;
    line-height: var(--form-input-height);
    pointer-events: none;
}
.contactForm label input:focus ~ .formInputHint, .contactForm label textarea:focus ~ .formInputHint, .formInputHint.off {
    top: -16px !important;
    font-size: 10px;
    opacity: 0;
}

.formSubmitButton {
    width: 50%;
    height: calc(var(--form-input-height) / 1.2);
    margin-top: 25px;
    
    -ms-flex-item-align: end;
    
        align-self: flex-end;
    font-size: 13.5px;
    
    color: white;
    border: none;
    background-color: var(--nav-blue);
    cursor: pointer;
}
.formSubmitButton:hover {
    opacity: 0.8;
}

.validInput, .invalidInput {
    position: absolute;
    top: 13px;
    right: 0;
    
    width: 24px;
    height: 24px;
    
    background-size: contain;
}

.validInput {
    background-image: url('/icons/valid.svg');
}

.invalidInput {
    background-image: url('/icons/invalid.svg');
}

.formMessages {
    white-space: pre-line;
    color: red;
    
    font-size: 12px;
    font-weight: 600;
    line-height: 16px;
    
    text-align: center;
    
    padding: 10px;
    
    background: var(--grey);
    border-right: 5px solid #eee;
    
}
.formMessages:before {
    content: '! ';
}
.formMessages:after {
    content: ' !';
}

.gallery {
    display: block;
      
    /* -webkit-column-count: var(--ref-columns);
    -moz-column-count: var(--ref-columns); 
    column-count: var(--ref-columns); */
    
    width: 775px; /* 3* 250 + 5 * 5 px */
  
