.galleryPictureWrapper {    
    display: inline-block;
    position: relative;
    
    max-width: var(--gallery-picture-width);
    max-height: var(--gallery-picture-height);
    width: var(--gallery-picture-width);
    height: var(--gallery-picture-height);
    
    margin-left: var(--gallery-picture-padding);

    overflow: hidden;
    
    border: 1px solid #ddd;
    cursor: pointer;
}

.curtain { 
    position: absolute;
    top: var(--gallery-picture-padding);
    left: var(--gallery-picture-padding);
    width: calc(100% - var(--gallery-picture-padding) * 2);
    height: calc(var(--gallery-picture-height) - var(--gallery-picture-padding) * 2);
    background: white;
    
  
    z-index: 1;
    
}
.plus {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
        -ms-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
    
    display: block;
    
    width: calc(100% - var(--gallery-picture-padding) * 2);
    height: 240px;
    
    font-size: 60px;
    text-align: center;
    line-height: 240px;
    
    color: white;
    
    pointer-events: none;
    opacity: 0;
    z-index: 2;
}

.curtain:hover ~ .plus {
    opacity: 1;
}

.galleryPictureWrapper:before {
  content: " ";
  position: absolute;
  z-index: 1;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  border: 5px solid var(--grey);
}

.galleryPicture {
/*
    width: 250px;
    height: auto;
*/
}

.galleryCurtain {
    position: fixed;
    top: 0;
    left: 0;
    
    width: 100%;
    width: 100vw;
    height: 100%;
    height: 100vw;
    
    
    background: var(--light-black);
    opacity: 0.5;
    z-index: 60;
}

.galleryScreen {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    
    display: block;
    
    width: 667px;
    max-width: var(--gallery-screen-max-width);
    height: var(--gallery-screen-height);
    
    margin: auto;
    padding: calc(var(--gallery-picture-padding) * 2);
    border-radius: var(--gallery-screen-border-radius);
    
    background: white;
    z-index: 65;
}

.galleryScreen > div {
    position: absolute;
    background: white;
}

.leftPicture:hover, .rightPicture:hover {
    color: #eee;
}

.closeGalleryScreen:hover {
    opacity: 0.5;
    cursor: pointer;
}

.galleryScreen .closeGalleryScreen {
    top: calc(-1 * var(--gallery-screen-close-handle-size) / 2);
    right: calc(-1 * var(--gallery-screen-close-handle-size) / 2);
    
    width: var(--gallery-screen-close-handle-size);
    height: var(--gallery-screen-close-handle-size);
    border-radius: 50%;
    
    line-height: var(--gallery-screen-close-handle-size);
    text-align: center;
    
    -webkit-box-shadow: -0.5px 1px 3px 0px var(--light-black);
    
            box-shadow: -0.5px 1px 3px 0px var(--light-black);
    z-index: 70;
}
.galleryScreen .leftPicture, .galleryScreen .rightPicture {
    top: calc((var(--gallery-screen-height) - var(--gallery-screen-control-height)) / 2);
    
    width: var(--gallery-screen-control-width);
    height: var(--gallery-screen-control-height);
    line-height: var(--gallery-screen-control-height);
    text-align: center;
    cursor: pointer;
}
.galleryScreen .leftPicture {
    left: calc(-1 * var(--gallery-screen-control-width));
    border-radius: var(--gallery-screen-border-radius) 0 0 var(--gallery-screen-border-radius);
/*    box-shadow: -0.5px -0.5px 3px 0px var(--light-black);*/
}

.galleryScreen .rightPicture {
    right: calc(-1 * var(--gallery-screen-control-width));
    border-radius: 0 var(--gallery-screen-border-radius) var(--gallery-screen-border-radius) 0;
/*    box-shadow: 0.5px 0.5px 3px 0px var(--light-black);*/
}

.galleryScreen > img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    max-width: var(--gallery-screen-max-width);
    max-height: var(--gallery-screen-height);
    margin: auto;
}



.footer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;

    height: var(--footer-height);
    
    padding-left: var(--nav-footer-padding);
